@media screen and (max-width: 840px) {

	nav {
	  border: 1px solid yellow;
	  background-color: rgba(0,0,0,0.5);
	  height: 50px;
	  position: fixed;
	  top:0;
		//clear:both;
		float: left;
	  width:45%;
	  letter-spacing: .2em;
	  z-index: 100;
	  padding-top:12px;
	  padding-bottom: 12px;
	  padding-left: 10px;
	  //padding-right: 10px;
	  text-shadow: 1px 1px 1px black;
	}

	nav ul li {
	  float: right;
	  margin: 0 20px;
	}

	nav li a:hover {
